:root {
  --color-white: hsl(0, 0%, 100%);
  --color-light-grey: hsl(0, 0%, 81%);
  --color-bg: hsl(210, 46%, 95%);
  --color-violet: hsl(263, 55%, 52%);
  --color-dark-grey: hsl(219, 29%, 14%);
  --color-grey: hsl(217, 19%, 35%);
}

* {
  box-sizing: border-box;
}

h2, p {
  margin: 0;
}

body {
  min-height: 100vh;
  background-color: var(--color-bg);
  font-size: 1.6rem;
  font-family: "Barlow Semi Condensed", sans-serif;
  line-height: 1.5;
}

.testimonial {
  width: 85%;
  max-width: 75rem;
  margin: 0 auto;
}
.testimonial__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 2rem;
}
.testimonial__item {
  padding: 1.25rem;
  border-radius: 0.3125rem;
  color: var(--color-white);
  font-size: 0.7em;
}
.testimonial__item .wrapper-author {
  display: flex;
  align-items: center;
}
.testimonial__item .wrapper-author__img {
  border-radius: 50%;
  border: solid 2px white;
}
.testimonial__item .wrapper-author .wrapper-name {
  padding: 0 1.25rem;
  font-size: 0.5em;
}
.testimonial__item .heading {
  margin: 1.25rem 0;
  font-size: 1.1em;
}
.testimonial__item .detail {
  color: var(--color-light-grey);
  font-size: 0.7em;
}
.testimonial__item--violet {
  grid-column: 1/3;
  grid-row: 1/2;
  background-color: var(--color-violet);
}
.testimonial__item--light-grey {
  grid-column: 3/4;
  grid-row: 1/2;
  background-color: var(--color-grey);
}
.testimonial__item--white {
  grid-column: 1/2;
  grid-row: 2/3;
  background-color: var(--color-white);
  color: black;
}
.testimonial__item--white .detail {
  color: var(--color-grey);
}
.testimonial__item--dark-grey {
  grid-column: 2/4;
  grid-row: 2/3;
  background-color: var(--color-dark-grey);
}
.testimonial__item--white-long {
  grid-column: 4/5;
  grid-row: 1/3;
  background-color: var(--color-white);
  color: black;
}
.testimonial__item--white-long .detail {
  color: var(--color-grey);
}

.attribution {
  margin: 1.875rem;
  text-align: center;
}